// Alias for Lucide Icons
LX.LucideIconAlias = {
    'Stop': 'Square',
    'Refresh': 'RefreshCw',
    'Left': 'ChevronLeft',
    'Right': 'ChevronRight',
    'Up': 'ChevronUp',
    'Down': 'ChevronDown',
    'MenuArrows': 'ChevronsUpDown',
    'RotateForward': 'RotateCw',
    'RotateRight': 'RotateCw',
    'RotateBack': 'RotateCcw',
    'RotateLeft': 'RotateCcw'
};

// Generate Alias icons

export default LX.ICONS = ( () => {
    const aliasIcons: Record<string, any> = {};

    for ( let i in RAW_ICONS )
    {
        const aliases = RAW_ICONS[i][2];
        aliases.forEach( ( a: string ) => aliasIcons[a] = i );
    }

    return { ...RAW_ICONS, ...aliasIcons };
} )();
